import React from "react";
import {Link } from "react-router-dom";


function Header(){
	return(
		<div className="header">
			<h1>Welcome to the Potters Touch Club</h1>
			<nav>
				<ul>
					<Link to="/add-member">Become a member</Link> 
					|
					<Link to="/">See all Members</Link>
					|
					<Link to="/get-member">Search member</Link>
				</ul>
			</nav>
		</div>
		)
}

export default Header;