import React,{useState} from "react";
import Header from "./header.jsx";

const GetMember  = () => {

const [userID,setUserId] = useState("");
const [userName,setUserName] = useState("");
const [member,setMember] = useState("");

	const handleSubmit = async(e)=>{
		try{
			e.preventDefault();

			if(userID){
				const response = await fetch(`http://localhost:5000/get-member-id/${userID}`,
				{
					method:"GET",
					headers:{"Content-Type":"application/json"}
				})
				const memberRes = await response.json();
				memberRes.length > 0 ? setMember(memberRes[0]) : setMember(`No member with the id of ${userID}`)
			}
			if(userName){
				const response = await fetch(`http://localhost:5000/get-member-name/${userName}`,
				{
					method:"GET",
					headers:{"Content-Type":"application/json"}
				})
				const memberRes = await response.json();
				memberRes.length > 0 ? setMember(memberRes[0]) : setMember(`No member with the name ${userName}`)
			}
			
		}catch(e){
			console.error(e.message);
		}
	}

	const validateData = (param)=>{
		let knowParam = typeof param;
		if(knowParam === "object"){
		  return<tr>
		  			<td key="member.id">{member.id}</td>
		  			<td key="member.id">{member.name}</td>
		  			<td key="member.id">{member.email}</td>
		  			<td key="member.id">{member.gender}</td>
		  		</tr>
		}
		else{
				return <tr>
		  			<td colSpan="4">{param}</td>
		  		</tr>
		}
	}
	
	return(
		<>
		<Header/>
		<div className="getMemberByAllMeans">
			<div className="getMemberByID">
				<h4 id="searchInfo">Enter the Id of a member to search </h4>
				<form onSubmit={handleSubmit}className = "getMember">
					<input type="number"  onChange={(e)=>{setUserId(e.target.value)}}/>
					<input className="submitBtn" type="submit"/>
				</form>
			</div>
			<div className="getMemberByName">
				<h4 id="searchInfo">Enter the Name of a member to search </h4>
				<form onSubmit={handleSubmit}className = "getMember">
					<input type="text"  onChange={(e)=>{setUserName(e.target.value)}}/>
					<input className="submitBtn" type="submit"/>
				</form>
			</div>
		</div>
		<table >
			  <thead>
			    <tr>
			      <th scope="col">ID</th>
			      <th scope="col">Name</th>
			      <th scope="col">Email</th>
			      <th scope="col">Gender</th>
			    </tr>
			  </thead>
			  <tbody>
		  			{validateData(member)}
			  </tbody>
			</table>
		
		</>
		)
}
export default GetMember;