import React,{useState}from "react";

const EditMember = ({memberProp})=>{
	const [edition,setEdition] = useState(memberProp);
	const [style,setStyle] = useState("none");
	
	function modalOpen(){
		setStyle("block");

	}
	function modalClose(){
		setStyle("none");
		setEdition(memberProp);
	}
	
	const editMember = async()=>{
		try{
			 await fetch(`http://localhost:5000/all-members/${edition.id}`,
				{ method:"PUT",body : JSON.stringify(edition),headers:{"Content-Type":"application/json"}}) 
			window.location.reload();
		}catch(e){
			console.error(e.message);
		}
	}
		return(
			<>
			<button onClick={modalOpen} className="btn btn-success" id="editBtn">edit</button>

			<div className="modalBg" style={{display:`${style}`}}>
				<div className="modal">
					<div className="modalInner">
					
						<label className="nameLabel">Name</label>
						<input type="text" value={edition.name} 
						onChange={(e)=>{setEdition({...edition,name:e.target.value})}}/>

						<label>email</label>
						<input type="email" value={edition.email} 
						onChange={(e)=>setEdition({...edition,email:e.target.value})}/>

						<label>Gender</label>
						<div>
							<div>
								<input type="radio" name="Gender" value="male" 
								onChange={(e)=>{setEdition({...edition,gender:e.target.value})}} />
								<label >Male</label>
							</div>
							<div>
								<input type="radio" name="Gender" value="female"
								onChange={(e)=>{setEdition({...edition,gender: e.target.value})}} />
								<label >Female</label>
							</div>
						</div>

						<div className="modalButtons">
							<button onClick={modalClose}className="closeBtn">close</button>
							<button onClick={()=>editMember()}className="editBtn">update</button>
						</div>

					</div>
				</div>
			</div>
			</>
		)
}

export default EditMember;