import './App.css';
import {BrowserRouter as Router, Route} from "react-router-dom";
import AddMember from "./components/addMember.jsx";
import AllMembers from "./components/allMembers.jsx";
import GetMember from "./components/getMember.jsx";

function App() {
  return (
    <Router>
      <div className="App">
        <Route exact path="/" component = {AllMembers}/>
        <Route path="/add-member" component = {AddMember}/>
        <Route path="/get-member" component = {GetMember}/>
      </div>
    </Router>
  );
}

export default App;
