import React,{useState,useEffect} from "react";
import EditMember from "./editMember.jsx";
import Header from "./header.jsx";


function AllMembers() {
	const [members,setMembers] = useState("");

	//get all members and put them into state
	async function getMembers(){
		try{
			const response  = await fetch("http://localhost:5000/all-members",
			{
				method:"GET",
				headers:{"Content-Type":"application/json"}
			})
			const responseArr = await response.json();
			setMembers(responseArr);
		}catch(e){
			console.error(e.message);
		}		
	}
	

	//clone members array to better map the output
	let membersClone  = [];
	for(let i= 0; i<members.length;i++){
		membersClone.push(members[i]);
	}
	

	//Delete member
	const delMember = async (id,name)=>{
		try{
			const res = await fetch(`http://localhost:5000/all-members/${id}`,
			{
				method : "DELETE",
				headers : {"Content-Type":"application/json"}
			})
			const delResponse = await res.json();
			alert(`${name} has been deleted from Potter's Touch Club's list!`);
			setMembers(delResponse)
		}catch(e){
			console.error(e.message);
		}

	}

	useEffect(()=>getMembers(),[]);
	return(
		<>
			<Header/>
			<table >
			  <thead>
			    <tr>
			      <th scope="col">#</th>
			      <th scope="col">Name</th>
			      <th scope="col">Email</th>
			      <th scope="col">Gender</th>
			      <th scope="col">Edit</th>
			      <th scope="col">Delete</th>
			    </tr>
			  </thead>
			  <tbody>
		  		{membersClone.map(x=> 
		  			<tr key={x.id} >
		   				<td>{x.id}</td>
			 		    <td>{x.name}</td>
			 		    <td>{x.email}</td>
			 		    <td>{x.gender}</td>
			 		    <td><EditMember memberProp = {x}/></td>
			 		    <td><button onClick={(()=>{delMember(x.id,x.name)})} id="delBtn">Delete</button></td>
	 				</tr>	)
		  		}
			  </tbody>
			</table>
		</>
		)
}

export default AllMembers;