 import React from 'react';
import Header from "./header.jsx";

class AddMember extends React.Component{

	state =  {
		name: " ",
		email: " ",
		gender: " "
	}

	handleSubmit = async (e) =>{
		e.preventDefault();
		try{
			const body  = this.state;
			const response = await fetch( "http://localhost:5000/add-member",
			{
				method:"POST",
				body : JSON.stringify(body),
				headers:{"Content-Type":"application/json"}
			}
			)
			 const newMember = await response.json();
			 alert(`Congrats!\n${newMember[0].name.toUpperCase()} with the id of ${newMember[0].id},\n has been added to the Potter's touch members's list!`)
			
		}catch(e){
			console.error(e.message);
		}
		
	}


render(){
	return(
		<>
		<Header/>
		<form onSubmit = {this.handleSubmit}>
			<label>Name</label>
			<input id="name" type="text" onChange={(e)=>{this.setState({name: e.target.value})}} />
			
			<label>Email</label>
			<input id="email" type="email" onChange={(e)=>{this.setState({email: e.target.value})}}/>
			
			<label>Gender</label>
			<div>
				<div>
					<input type="radio" name="Gender" value="male" 
					onChange={(e)=>{this.setState({gender: e.target.value})}} />
					<label >Male</label>
				</div>
				<div>
					<input type="radio" name="Gender" value="female"
					onChange={(e)=>{this.setState({gender: e.target.value})}} />
					<label >Female</label>
				</div>
			</div>
			<input className="submitBtn"type="submit"/>
		</form>
		</>
		)
	}
}
export default AddMember;